/* eslint-disable @typescript-eslint/no-explicit-any */
"use client"

import * as React from "react"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { extractArray } from "@/lib/normalize"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"

export default function Page() {
  const [q, setQ] = React.useState("")
  const [rows, setRows] = React.useState<any[]>([])
  const [err, setErr] = React.useState<string | undefined>()
  const [loading, setLoading] = React.useState(false)

  // filters
  const [brands, setBrands] = React.useState<any[]>([])
  const [brandId, setBrandId] = React.useState<string>("")
  const [cats, setCats] = React.useState<any[]>([])
  const [gaId, setGaId] = React.useState<string>("")
  const [plate, setPlate] = React.useState("")
  const [carId, setCarId] = React.useState<string>("") // kType

  React.useEffect(() => {
    ;(async () => {
      try {
        const b = await (await fetch("/api/brands")).json()
        setBrands(extractArray(b))
      } catch {}
      try {
        const c = await (await fetch("/api/categories")).json()
        setCats(extractArray(c))
      } catch {}
    })()
  }, [])

  async function resolvePlate() {
    if (!plate) return
    setErr(undefined)
    try {
      const r = await fetch(`/api/plates?plate=${encodeURIComponent(plate)}&country=nl`)
      const j = await r.json()
      if (!r.ok) throw new Error(j?.error || j?.statusText || "Kenteken lookup mislukt")
      const first = extractArray(j)[0]
      if (first?.carId) setCarId(String(first.carId))
      else throw new Error("Geen voertuig gevonden voor dit kenteken")
    } catch (e: any) {
      setErr(e.message)
    }
  }

  async function run() {
    setLoading(true)
    setErr(undefined)
    try {
      const p = new URLSearchParams()
      if (q) p.set("q", q)
      if (brandId) p.set("brandNo", brandId)
      if (gaId) p.set("genericArticleId", gaId)
      if (carId) {
        p.set("carId", carId)
        p.set("linkingTargetType", "P")
      }

      const r = await fetch(`/api/search?${p.toString()}`)
      const j = await r.json()
      if (!r.ok) throw new Error(j?.error || "Zoekopdracht mislukt")
      setRows(extractArray(j))
    } catch (e: any) {
      setErr(e.message)
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="px-4 lg:px-6 space-y-4">
      <Card className="rounded-2xl">
        <CardHeader>
          <CardTitle>Zoek op artikel</CardTitle>
        </CardHeader>
        <CardContent className="grid gap-3 md:grid-cols-2 lg:grid-cols-3">
          <div className="flex gap-2">
            <Input value={q} onChange={(e) => setQ(e.target.value)} placeholder="Artikelnaam / nummer" onKeyDown={(e) => e.key === "Enter" && run()} />
            <Button onClick={run} disabled={loading}>{loading ? "Zoeken…" : "Zoek"}</Button>
          </div>

          <div className="flex items-center gap-2">
            <label className="w-24 text-sm text-muted-foreground">Merk</label>
            <Select value={brandId} onValueChange={setBrandId}>
              <SelectTrigger className="h-9"><SelectValue placeholder="Alle merken" /></SelectTrigger>
              <SelectContent>
                {brands.map((b: any) => (
                  <SelectItem key={b.id} value={String(b.id)}>{b.name}</SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          <div className="flex items-center gap-2">
            <label className="w-24 text-sm text-muted-foreground">Categorie (GA)</label>
            <Select value={gaId} onValueChange={setGaId}>
              <SelectTrigger className="h-9"><SelectValue placeholder="Alle categorieën" /></SelectTrigger>
              <SelectContent className="max-h-80">
                {cats.map((c: any) => (
                  <SelectItem key={c?.id ?? c?.genericArticleId} value={String(c?.id ?? c?.genericArticleId)}>
                    {c?.name ?? c?.genericArticleDescription}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          <div className="flex items-center gap-2">
            <label className="w-24 text-sm text-muted-foreground">Kenteken</label>
            <Input value={plate} onChange={(e) => setPlate(e.target.value)} placeholder="bv. G123AB" />
            <Button variant="secondary" onClick={resolvePlate}>Koppel voertuig</Button>
          </div>

          <div className="flex items-center gap-2">
            <label className="w-24 text-sm text-muted-foreground">KType (carId)</label>
            <Input value={carId} onChange={(e) => setCarId(e.target.value)} placeholder="Bijv. 27563" />
          </div>

          {err && <span className="text-sm text-rose-600">{err}</span>}
        </CardContent>
      </Card>

      <Card className="rounded-2xl">
        <CardHeader><CardTitle>Resultaten</CardTitle></CardHeader>
        <CardContent className="px-0">
          <div className="overflow-x-auto">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>ID</TableHead>
                  <TableHead>Naam</TableHead>
                  <TableHead>Merk</TableHead>
                  <TableHead>GA</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {rows.length === 0 && (
                  <TableRow><TableCell colSpan={4} className="text-muted-foreground">Geen resultaten</TableCell></TableRow>
                )}
                {rows.map((r: any) => (
                  <TableRow key={r?.articleId ?? r?.id}>
                    <TableCell className="font-medium">{r?.articleId ?? r?.id}</TableCell>
                    <TableCell>{r?.articleName ?? r?.name}</TableCell>
                    <TableCell>{r?.supplierName ?? r?.brand}</TableCell>
                    <TableCell>{r?.genericArticleName ?? r?.genericArticleDescription}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
